:root{
  --primary:#4e4e4e;
  --secondary:#ffddaa;
  --error:#ff4a4a;
}
.App{
  max-width: 960px;
  margin: 0 auto;
  padding: 0 20px;
}
.title{
  /* border-bottom: 2px solid var(--secondary); */
  height: 50px;
}
.title h1{
  padding: 0px 10px;
  color: var(--primary);
  letter-spacing: 2px;
}
.upload-form{
  /* background: #000; */
  width: 30px;
  margin: 20px auto;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.upload-form button{
  background: none;
  border: none;
  border: 2px solid var(--secondary);
  color: var(--primary);
  font-size: 1.5rem;
  border-radius: 50px;
}
.upload-form input[type='file']{
  position: absolute;
  left: 0;
  top: 0;
  font-size: 3rem;
  opacity: 0;
}
.progress-bar{
  height: 3px;
  /* width: 100%; */
  background: var(--secondary);
  transition: 0.5s;
  border-radius: 50px;
}
.img-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  
}
.img-wrap{
  height: 250px;
  width: 250px;
  margin: 20px auto;
  padding: 0 10px;
}
.img-wrap img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.backdrop img{
  display: block;
  max-width: 80%;
  max-height: 70%;
  margin: 50px auto;
  box-shadow: 1px 1px 5px 2px rgba(0, 0, 0, 0.418);
}
